@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@500;600&display=swap');

  .App {
 width: 100%;
 margin:auto;
 font-family: poppins!important;

}


.body{font-family: 'Poppins', sans-serif;}

.App-header {
  background-color: #f1d534;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: black;
}

.App-link {
  color: #61dafb;
}

.footer{padding:100px 0;width:100%; color: #393D42; background-color: #f1d534; border-style: solid; border-color:#f1d534;text-align:center}
 .footer span.copyright{color:#D2691E;font-size:90%;line-height:40px;text-transform:none;}
 .footer ul.quicklinks{cursor:pointer;color:#D2691E;font-size:90%;line-height:40px;margin-bottom:0;text-transform:none;}
 .footer ul.quicklinks a {color:#D2691E}
 ul.social-buttons{margin-top:15%; padding-top:10%;}
ul.social-buttons li a{font-size:20px;line-height:50px;display:block;width:50px;height:50px;transition:all .3s;color:#f1d534;border-radius:100%;outline:0;background-color:#393D42}ul.social-buttons li a:active,ul.social-buttons li a:focus,ul.social-buttons li a:hover{background-color:#696969}
 
 .space{padding:100px 0;width:100%; color: #393D42; background-color: #46a5a4;}

 .dir{
  text-align: left;
  align-items: center;
  margin-left: 30%
 }

 .overlayt {
  position: relative;
  text-align: center;
  color: white;
  margin:auto;

}

.pics-wrapper{
  position: relative;
  margin:auto;
}

.pics-wrapper2{
   position: relative;
  margin:auto;
  width:70%;
  padding: 1.5% 0 5% 0;
}

.textwrap{
  padding-top: 3%;
  margin:auto;
  align-items: center;
  text-align: justify;
  width:50%;
}
.textwrap h1{
  padding-bottom: 2.5%;
  text-align: center;
  font-weight: bold;
}
.pics-wrapper2 img{ margin: 0% 0% 0% 0%; }

.pics-wrapper:hover img { opacity: 0.5; }
.pics-wrapper img:hover { opacity: 1; cursor: pointer; } 
.pics-wrapper2:hover img { opacity: 0.5; }
.pics-wrapper2 img:hover { opacity: 1; cursor: pointer; } 


.border {border: 4% solid rgba(255,255,255, .8);}
.border2 {border: 8% solid rgba(255,255,255, .8); }

.row {
  display: flex;
  flex-wrap: wrap;
  margin: auto;
}

.column {
  flex: 50%;
  max-width: 50%
}
.column2 {
  flex: 25%;
  max-width: 25%

}

.column3 {
  flex: 50%;
  max-width: 50%

}

.column4 {
  flex: 100%;
  max-width: 100%

}

.column5 {
  flex: 25%;
  max-width: 33%;
 

}
.column6 {
  flex: 66%;
  max-width: 66%

}


.ove{
  position: relative;
  margin:auto;
  text-align: center;
}

.ove img:hover {  cursor: pointer;} 

.img-hover-zoom {
  /* [1.1] Set it as per your need */
  overflow: hidden; /* [1.2] Hide the overflowing of child elements */
}

/* [2] Transition property for smooth transformation of images */
.img-hover-zoom img {
  
  transition: transform .5s ease;
}


/* [3] Finally, transforming the image when container gets hovered */
.img-hover-zoom:hover img {
  transform: scale(1.1);
}







@media screen and (max-width: 601px) {


.overlayt {
  
  text-align: center;
 
  margin:auto;

}



.dir{
  text-align: left;
  align-items: center;
